<template>
    <div :class="prefixCls">
        <slot></slot>
    </div>
</template>
<script>
    const prefixCls = 'yy-btn-group'
    export default {
        name: 'YYButtonGroup',
        data() {
            return {
                prefixCls: prefixCls
            }
        }
    }
</script>



